import React from "react";
import TableComponents from "../Table/TableComponents";
import TablePagination from "../Table/TablePagination";
import { Popconfirm, message } from "antd";

function RejectedVendors() {
  const confirm = (e) => {
    console.log(e);
    message.success("Click on Yes");
  };
  const cancel = (e) => {
    console.log(e);
    message.error("Click on No");
  };

  return (
    <div>
      <div className="row">
        <div className="col-md-12">
          <TableComponents />
        </div>
      </div>
      <div className="row my-4">
        <div className="col-md-12">
          <div className="table-wrapper table-responsive">
            <table className="table mb-0">
              <thead>
                <tr>
                  <th>S. No.</th>
                  <th>Company Name</th>
                  <th>Company Address</th>
                  <th>Contact Name</th>
                  <th>Rejection Date</th>
                  <th>Request Date</th>
                  <th>Status</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {[...Array(10)].map((element, index) => {
                  return (
                    <tr key={index + 1}>
                      <td>{index + 1}</td>
                      <td>Enumeral Waste</td>
                      <td>abc street, xyz city</td>
                      <td>Lorem ipsum</td>
                      <td className="ws-nowrap">11/ 09/ 2023, 11:45 PM</td>
                      <td className="ws-nowrap">11/ 09/ 2023, 11:45 PM</td>
                      <td>
                        <p className="mb-0 red-color fw-500 fs-16">Rejected</p>
                      </td>
                      <td>
                        <Popconfirm
                          title="Delete the task"
                          description="Are you sure to approve this user?"
                          onConfirm={confirm}
                          onCancel={cancel}
                          okText="Yes"
                          cancelText="No"
                        >
                          <button className="border-0 bg-none primary-color fw-500 fs-16 ws-nowrap">
                            Aprrove Now
                          </button>
                        </Popconfirm>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div className="row mb-4">
        <div className="col-md-12">
          <TablePagination />
        </div>
      </div>
    </div>
  );
}

export default RejectedVendors;
